h1, h2, h3, p {
    font-family: 'Montserrat';
    text-align: center;
}

a {
    color: blueviolet;
    text-decoration: none;
}

a:hover {
    color: darkmagenta;
}

.content {
    text-align: left;
    padding-left: 100px;
    padding-right: 100px;
    font-weight: 400;
    display: block;
    box-sizing: border-box;
    position: relative;
}

.column {
    box-sizing: border-box;
    float: left;
}

div.column {
    width: 30%;
    padding-top: 2%;
    padding-left: 1%;
    padding-right: 5%;
}

// For Tableau Embedding
#tableauDashboard-container {
    height: 80%;

    min-width: 66%;
    margin-right: 0;
    box-sizing: border-box;
    float: left;
    margin-left: 2%;
    margin-right: 2%;
}

// For US Map Vis
#mapVis {
    max-width: 70%;
}


// For specific pages
.p1 {
    background: url('../images/unsplash-covid19.jpg');  // https://unsplash.com/photos/EAgGqOiDDMg
    background-size: cover;

    // h1 {
    //     position: absolute;
    //     top: 20%;
    //     left: 40%;
    //     color: whitesmoke;
    //     font-size: 4rem;
    //     text-align: right;
    //     margin-right: 1em;
    // }

    h1, h3 {
        color: whitesmoke;
        text-align: right;
        margin-right: 5%;
    }

    h1 {
        font-size: 3rem;
        margin-bottom: 0;
    }
}

.p2 {
    div {
        margin-bottom: 60px;
    }
}

.p3 {
    p {
        display: block;
        margin-block-start: 1em;
        margin-block-end: 1em;
        margin-inline-start: 0px;
        margin-inline-end: 0px;
        font-size: 120%;
    }

    h1 {
        display: block;
        position: relative;
        text-align: left;
    }
}

.p4 {
    h1 {
        text-align: left;
        margin-left: 6%;
    }
}

.p7 {
    h2, p{
        text-align: left;
    }
    h1 {
        padding: 2px 6px;
    }
}

.p12 {
    .content {
        position: absolute;
        top: 40px;
        left: 40px;
    }
    
    p, h1, h2 {
        text-align: left;
    }
}


span {  // For the navigation points on the right
    background: black;  //!important otherwise covered
}

.fp-slidesNav.fp-bottom {
    margin-left: 1em !important;
}




// For testing pages used

.s1 {
    background: url('../images/mkm1.jpg');
    background-size: cover;
}

.s2 {
    background: url('../images/mkm2.jpg');
    background-size: cover;

    h1 {
        text-align: left;
        margin-left: 2em;
        font-size: 4rem;
    }
}

.s3 {
    background: url('../images/mkm3.jpg');
    background-size: cover;

    h1 {
        text-align: right;
        margin-right: 2em;
        font-size: 4em;
    }
}
